	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 2 - Attaque au parc","texte":{"childMarkdownRemark":{"html":"<p>Un gang s’approche de Kellan et semblent vouloir profiter de sa panique. Un des hommes essaie de planter un couteau dans les côtes de Kellan, mais la lame se brise en mille morceau. Le gars recule et fait signe aux autres de reculer aussi en montrant le couteau. Kellan revient à lui et regarde les hommes qui l’entourent.</p>\n<p>Kellan: Qu’est-ce que vous me voulez?<br>\nHomme 1: Rien mec, tout va bien.<br>\nKellan: Tout va bien? Tout va bien! Non mais t’as vu ce qui c’est passé avec moi?</p>\n<p>Les hommes reculent et semblent avoir peur. J’attrape le chandail du premier homme à ma porté. Les autres gars autour sortent des armes à feu et me pointent avec. Mes yeux deviennent rouge de colère et je lance l’homme que je tien sur un autre homme. Les deux hommes font un vol d’au moins 10 mètres. Les cinq autres hommes commencent à me tirer dessus. Je me protège le visage avec mon bras, mais je sens les balles de pistolet comme si c’était des simples boules de neiges que les gens me lancent. Je fonce vers l’un des hommes et l’envoie valser sur un banc.</p>\n<p>Le gang se réduit à force que je mette les hommes KO les uns après les autres. Je commence à revenir à moi lorsque le dernier m’implore de l’épargner alors que j’allais lui donner le coup de poing fatal. Je le lâche et commence à courir dans l’autre direction. Les sirènes des voitures de police commencent à se faire entendre. Les coups de feu avaient dû faire paniquer les voisins. </p>\n<p>Où est-ce que je peux me cacher en attendant que ça passe? La forêt peut-être? Je passe mon énorme main dans mes cheveux face à cette interrogation. Je vais aller dans la forêt, c’est la seule solution que je vois en ce moment. Je me met à courir vers la forêt. Les sirènes se rapprochent du parc. J’entre dans la forêt et va assez loin pour que la police ne puisse pas me voir. Lorsque je suis sûr que tout va bien, je m'assois contre un arbre et repense à tout ça. Je regarde mes mains, mes bras, mon torse avec mon chandail assez déchiré et abîmé à cause des tirs de pistolet et du coup de couteau. Mon pantalon qui est maintenant l’équivalent d’un short serré. Si je suis pour rester comme ça, mes vêtements feront jamais l’affaire. Pourquoi je suis rendu comme ça justement. Je fini par m’endormir avec toutes ses questions.</p>\n<p>À mon réveil, je me rend compte que ce n’était pas un mauvais rêve. Je suis toujours un géant, mon corps n’a pas changé. Je suis devenu un monstre, mais pas n’importe quel genre de monstre, maintenant je serais capable de me défendre et d’obtenir tout ce que je veux. Je me lève avec la détermination d’un nouvel homme, je regarde autour de moi.</p>\n<p>Kellan: Bon… c’est par où que je devrais sortir?</p>\n<p>Je me mord la lèvre et essaie de penser à par où j’étais arrivé hier soir. Il faisait pas mal sombre la nuit dernière, alors certains détails devraient me sauter aux yeux. Je continue de regarde autour de moi. L’arbre qui était tombé, le bout qui touche le sol pointait vers ma droite. Alors si je me met en face et que je fais demi-tour. Je devrais arriver à sortir du bois. J’entend une voiture qui passe sur une rue proche. J’ai juste à suivre le bruit vers où la voiture était.</p>\n<p>Je commence à marcher dans cette direction et j’arrive au bord de la forêt. Il y avait des barrières de sécurité et des policiers avec des ambulanciers qui ramassaient les corps des hommes qui m’ont attaqués hier. Je sors de la forêt et décide de passer par une autre rue. Les gens me regardent et semblent se demander ce qui m’est arrivé. Je ne les regarde pas. J’ai juste envie de rentrer chez moi et de me coucher dans mon lit. Les gens se tassent pour me laisser passer, et certains ont clairement peur de moi. Je suis vraiment différent des autres. Je mesure une tête voir deux têtes de plus que les autres. J’arrive enfin chez moi, mon voisin me reconnait et me regarde avec des gros yeux. Je l’ignore et entre chez moi en utilisant ma clé. J’ouvre la porte et réalise que je dois pencher la tête si je veux entrer sans me cogner sur le haut du cadre de porte. J’entre donc chez moi et ferme la porte. Je tombe nez à nez avec ma mère. Elle commence à crier, effrayée.</p>\n<p>Ma mère: Mais qui es-tu!?<br>\nKellan: C’est moi, Kellan.<br>\nMa mère: Non! C’est impossible! Qu’est-ce que tu as fait?!<br>\nKellan: J’ai rien fais moi! D’un coup, j’ai changé, comme si on m’avait fait prendre un produit pour augmenter ma masse musculaire!<br>\nMa mère: Tu dis n’importe quoi! J’en ai assez de tes mensonges!</p>\n<p>Mon père arrive dans la pièce et lève les yeux, maintenant c’était clair que je le dépassais.</p>\n<p>Mon père: Qu’est-ce que t’as foutu encore?<br>\nKellan: Mais j’ai rien fais je vous dis!<br>\nMon père: J’appelle la police, t’as du prendre une drogue ou quelque chose du genre.<br>\nKellan: Non! Tu n’appelles pas la police!</p>\n<p>J’attrape le téléphone avant mon père et réduit le téléphone en miette.</p>\n<p>Kellan: Oh merde…<br>\nMon père: Mais t’es complètement malade ou quoi?!<br>\nKellan: Peut-être! Je sais pas ce qui m’arrive pour que ça fasse ça.<br>\nMon père: Il n’est pas question que tu restes ici, il n’est pas question qu’un monstre reste chez moi.<br>\nKellan: Pour une fois que je ne suis pas une personne faible avec des os fragiles. Vous voulez me foutre dehors.</p>\n<p>Mes parents se taisent et se regardent, il était vrai que mes parents m’avaient souvent reproché d’être faible et fragile. Maintenant c’est complètement l’inverse. Mon père semble réfléchir. Ma petite soeur arrive dans la cuisine et lève les yeux vers moi. Elle commence à rire.</p>\n<p>Ophélia: Ahahah! Si tu voyais la tête de con que tu fais!<br>\nKellan: Oh toi ferme-la.<br>\nMa mère: Kellan.<br>\nKellan: Quoi?<br>\nMa mère: Tu t’es fais tirer dessus?<br>\nKellan: Oui, la nuit dernière…<br>\nMa mère: Pourquoi ta peau n’a rien?<br>\nKellan: J’en sais rien. J’ai juste envie d’aller me reposer dans ma chambre.<br>\nMon père: Non, tu vas tout nous expliquer avant.<br>\nKellan: J’étais assis dans le parc, j’ai commencé à avoir vraiment mal, partout sur moi et quelques secondes plus tard, j’étais rendu comme ça. Un gang m’a attaqué, ils ont essayé de me planter un couteau ou un truc comme ça et puis ils m’ont tiré dessus en voyant que les couteaux n’avaient rien fait. Je me suis énervé, je leur ai cassé la gueule et puis comme la police arrivait je suis allé me cacher dans la forêt. Je me suis réveillé, je suis rentré à la maison et voilà.<br>\nMa mère: Le gang dans le parc à côté?<br>\nKellan: Oui.<br>\nMa mère: Les 6 jeunes qui sont blessés sur le seuil de la mort?<br>\nKellan: Peut-être, mais ils m’ont tiré dessus comme si j’étais un truc à chasser.</p>\n<p>Ma mère recule effrayée.</p>\n<p>Ma mère: Aux nouvelles, ils disent qu’une bête sauvage tout s’être attaqué à eux.<br>\nKellan: Je ne suis pas une bête sauvage.</p>\n<p>Je vais vers ma chambre, fatigué de tout ça. Mes parents n’osent pas s’imposer, une bonne chose. Alors que je passe, ma soeur me dévisage. Toujours une petite peste pourrie gâtée qui a tout ce qu’elle veut. Je l’ignore, va dans ma chambre et ferme la porte.</p>"}},"chapitreAvant":"chapitre-1-fragile","chapitreApres":null,"nomRoman":"../le-mastodonte-des-black-eagles","slug":"chapitre-2-attaque-au-parc"}},"pathContext":{"slug":"chapitre-2-attaque-au-parc"}}

